body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    overflow-x: hidden;
}

.divSpace {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40px;
}

.App {
    display: flex;
    flex-direction: column;
}

.box-container {
    min-height: 100vh;
    width: 100vw;
    display: grid;
    justify-content: center;
    align-items: center;
    margin-bottom: 400px;
}

.box {
    height: 50px;
    width: 200px;
    text-align: center;
    position: relative;
    left: -200px;
}

.box:nth-child(1) {
    background-color: green;
}

.box:nth-child(2) {
    background-color: crimson;
}

.box:nth-child(3) {
    background-color: orange;
}

.box:nth-child(4) {
    background-color: yellow;
}

.box:nth-child(5) {
    background-color: green;
}

.box:nth-child(6) {
    background-color: crimson;
}

.box:nth-child(7) {
    background-color: orange;
}